.header {
  margin-bottom: 2rem;
}

.header ul {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}

.header ul li {
  margin-right: 1rem;
}

.link {
  display: block;
  text-decoration: none;
  font-size: 1.2rem;
  font-family: Arial, Helvetica, sans-serif;
  color: black;
  background-color: #eee;
  padding: 0.5rem 1rem;
  border-radius: 4px;
}
.active,
.link:hover {
  background-color: #ddd;
}

@media (max-width: 600px) {
  .header ul {
    justify-content: space-evenly;
  }

  .header ul li {
    margin: 0;
  }
}
